
/**
* SLIDER
*/
.slider-wrapper{
  position: relative;
overflow: hidden;
}
.slideshow-container {
  position: relative;
  max-height: 500px;
  margin: auto;
  overflow: hidden;
  -webkit-filter: brightness(0.7);
  filter: brightness(0.7);
}
.slider-image{
  width: 100%;
}
/* Hide the images by default */
.mySlides {
    display: none;
}

/* Next & previous buttons */
.prev, .next {
  display: none;
  background: rgba(0,0,0,.4);
  cursor: pointer;
  position: absolute;
  top: 50%;
  width: auto;
  margin-top: -22px;
  padding: 16px;
  color: white;
  font-weight: bold;
  font-size: 18px;
  -webkit-transition: 0.6s ease;
  -o-transition: 0.6s ease;
  transition: 0.6s ease;
  border-radius: 0 3px 3px 0;
}

/* Position the "next button" to the right */
.next {
  right: 0;
  border-radius: 3px 0 0 3px;
}

/* On hover, add a black background color with a little bit see-through */
.prev:hover, .next:hover {
  background-color: rgba(0,0,0,0.8);
}

/* Caption text */
.text {
  color: #f2f2f2;
  font-size: 34px;
  padding: 8px 12px;
  position: absolute;
  bottom: 8px;
  width: 100%;
  text-align: center;
  background:-webkit-gradient(linear, left top, right top, from(rgba(0,0,0,0)),color-stop(rgba(0,0,0,0.7)),to(rgba(0,0,0,0))) ;
  background:-webkit-linear-gradient(left, rgba(0,0,0,0),rgba(0,0,0,0.7),rgba(0,0,0,0)) ;
  background:-o-linear-gradient(left, rgba(0,0,0,0),rgba(0,0,0,0.7),rgba(0,0,0,0)) ;
  background:linear-gradient(to right, rgba(0,0,0,0),rgba(0,0,0,0.7),rgba(0,0,0,0)) ;
}

/* Number text (1/3 etc)
.numbertext {
  color: #f2f2f2;
  font-size: 12px;
  padding: 8px 12px;
  position: absolute;
  top: 0;
}
*/
/* The dots/bullets/indicators */
.dot {
  cursor: pointer;
  height: 15px;
  width: 15px;
  margin: 0 2px;
  background-color: #bbb;
  border-radius: 50%;
  display: inline-block;
  -webkit-transition: background-color 0.6s ease;
  -o-transition: background-color 0.6s ease;
  transition: background-color 0.6s ease;
}

.active-slide, .dot:hover {
  background: #717171!important;
}

/* Fading animation */
.fade {
  -webkit-animation-name: fade;
  -webkit-animation-duration: 5s;
  animation-name: fade;
  animation-duration: 5s;
}
@media screen and (max-width:768px) {
  .text{
    font-size: 18px;
  }
}
@-webkit-keyframes fade {
  0% {opacity: .4; transform: scale(1);-webkit-transform: scale(1);-moz-transform: scale(1);}
  100% {opacity: 1;transform: scale(1.03);-webkit-transform: scale(1.03);-moz-transform: scale(1.03);}
}

@keyframes fade {
  0% {opacity: .4; transform: scale(1);-webkit-transform: scale(1);-moz-transform: scale(1);}
  100% {opacity: 1;transform: scale(1.03);-webkit-transform: scale(1.03);-moz-transform: scale(1.03);}
}



