body,
html {
    padding: 0;
    margin: 0;
    font-family: 'Dosis', sans-serif;
}

button {
    color: #8d6b6b;
    border-radius: 5px;
    padding: 10px 20px;
    background: #e2bb28;
    text-transform: uppercase;
    box-shadow: 0 9px 6px -6px #313030;
    -webkit-box-shadow: 0 9px 6px -6px #313030;
}

button,
button:hover {
    -o-transition: .3s;
    transition: .3s;
    -webkit-transition: .3s;
    -moz-transition: .3s
}

button:hover {

    color: #222;
    cursor: pointer;
    margin-top: 2px;
    box-shadow: 0 7px 6px -6px #313030;
    -webkit-box-shadow: 0 7px 6px -6px #313030;

}

button:focus {
    outline: none
}

.my-button {
    color: #e4e4e4;
    background: #ca5555
}

.active {
  color: #353535!important;
  white-space: nowrap;
  background: -webkit-linear-gradient(left, #fafafa, hsla(0, 0%, 98%, 0));
  background: -moz-linear-gradient(left, #fafafa, hsla(0, 0%, 98%, 0));
  text-shadow: none!important;
  -webkit-animation: flow .2s ease-in;
  animation: flow .2s ease-in;
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards;

}

.active:hover {
    color: #a1a1a1!important;
    text-shadow: none!important;

}
.nav-active {
    background: hsla(0, 0%, 98%, .5);
    color: #e0e0e0!important
}

.nav-active:hover {
    color: #333!important;
    background: #fff;

}
.active-subMenu{
  background: #fff;
  color: #333;
}

.footer-active {
  color: #353535!important;
  white-space: nowrap;
  background: -webkit-linear-gradient(left, #fafafa, hsla(0, 0%, 98%, 0));
  background: -moz-linear-gradient(left, #fafafa, hsla(0, 0%, 98%, 0));
  text-shadow: none!important;
  -webkit-animation: flow .2s ease-in;
  animation: flow .2s ease-in;
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
}

li {
    list-style: none
}

.scroll-up {
    width: 40px;
    height: 40px;
    border-radius: 100%;
    position: fixed;
    bottom: 10px;
    right: 10px;
    cursor: pointer;
    -o-transition: .2s;
    transition: .2s;
    -moz-transition: .2s;
    -webkit-transition: .2s;
    z-index: 98;
}

.scroll-up:hover {
    filter: invert(100%);
    -moz-filter: invert(100%);
    -webkit-filter: invert(100%)
}




.header-wrapper {
  position: absolute;
  z-index: 97;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 100%;
  -ms-transform: translate(-50%,-50%);
