.special-box{
  text-align: center;
  background:-webkit-linear-gradient(to right, #dadada, #fff,#dadada);
  background:-webkit-gradient(linear, left top, right top, from(#dadada), color-stop(#fff),to(#dadada));
  background:-webkit-linear-gradient(left, #dadada, #fff,#dadada);
  background:-o-linear-gradient(left, #dadada, #fff,#dadada);
  background:linear-gradient(to right, #dadada, #fff,#dadada);
  margin:60px auto;
  position: relative;

}
.special:before, .special:after
{
  z-index: -1;
  position: absolute;
  content: "";
  bottom: 10px;
  left: 20px;
  width: 50%;
  top: 70%;
  max-width: 360px;
  background: #777;
  -webkit-box-shadow:0 18px 10px #777;
  box-shadow: 0 18px 10px #777;
  -webkit-transform: rotate(-3deg);
  -ms-transform: rotate(-3deg);
  transform: rotate(-3deg);
}
.special:after
{
  -webkit-transform: rotate(3deg);
  -ms-transform: rotate(3deg);
  transform: rotate(3deg);
  right: 10px;
  left: auto;
}
.special-wrapper{
  -o-transition: 0.2s;
  transition: 0.2s;
  -webkit-transition: 0.2s;
  -moz-transition: 0.2s;

}
.special-img{
  max-width: 400px;
