/**
* @ KEYFRAMES
*/
@keyframes contentShow {
  0%{height: 0%;}
  100%{height: 100%;}
}
@-webkit-keyframes contentShow {
  0%{height: 0%;}
  100%{height: 100%;}
}

@-webkit-keyframes flow {
    0% {width: 0}
    100% {width: 100%}
}
@keyframes flow {
    0% {width: 0}
    100% {width: 100% }
}



